}
.tab-container .tab.tab--profile::before, .tab-container .tab.tab--panel::before, .tab-container .tab.tab--settings::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 2.5rem;
  height: 2.5rem;
  background-size: contain;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .tab-container .tab.tab--profile::before, .tab-container .tab.tab--panel::before, .tab-container .tab.tab--settings::before {
    left: 2rem;
  }
}
